$color_1: #fff;
$font-family_1: 'Noto Sans', sans-serif;
$background-color_1: #0A2647;
$background-color_2:  #051831;
$background-color_3: darkgoldenrod;

// these are differenet color combos for the background color
// #144272 , #205295 , #2C74B3

/*-----------General---------*/
body {
	font-family: $font-family_1;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.6;
	color: $color_1;
	background-color: $background-color_1;
	margin-top: 180px;
	overflow: scroll;

}

ul {
	list-style: none;
	outline: none;
}

/*------------Elements-----------*/
.navbar {
	height: 75px;
}
.navbar-title { 
	font-size: 40px;
}

.button-class { // this is the pokemon buttons
	border: 3px solid;
	border-color: #fff;
	color: #fff;
	width: 225px;
	height: 250px;
	border-radius: 0.6rem;
	background-color: darkgoldenrod;
	text-transform: uppercase;
}

.button-class:hover { 
	opacity: 0.8;
}

.button-pokemonImg { 
	height: 80%;
}

.pokemon-list { 
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
	height: 100%;
	padding: 10px;
	background-color: $background-color_2;
	border-radius: 0.7rem;
	row-gap: 1rem;
}


/*-------Modal---------*/
.modal-content{ // the modal content
	display: flex;
	position: relative;
	flex-flow: column;
	background-color: darkgoldenrod;
	padding: 15px;
	height: 400px;
	width: 300px;
	margin: auto;
	border-radius: 0.6rem;
	border: 4px solid; 
}

.modal-title { // pokemon.name and close button
	text-transform: uppercase;
	font-size: 24px;
}

.modal-body { 
	padding: 0.5rem;
	text-align: center;
}

.modal-img { 
	margin-top: 20px;
	width: 50%;
	height: 170px;
}

.modal-height,
.modal-types {
	padding-top: 20px;
	margin: 0;
}
